
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.sidemenu {
  background-color: #363636;
  padding-right: 0px;
  display: block;
}
.sidemenu .menus {
}
.sidemenu .menus .menu-label {
  text-align: left;
  margin: 10px;
  color: #00D1B3;
  font-weight: 500;
  font-size: 12px;
}
.sidemenu .menus .item {
  display: block;
  padding: 5px;
  padding-left: 12px;
  text-align: left;
  font-weight: 600;
  font-size: 17px;
  margin: 4px;
  color: #afb8c3;
}
.sidemenu .menus .item:hover {
  background-color: #FFFFFF;
  border-radius: 4px;
  color: #363636;
}
.sidemenu .menus .item.active {
  background-color: #00D1B3;
  border-radius: 4px;
  color: #FFFFFF;
}
.sidemenu .menus .item.active:hover {
  background-color: #00D1B3;
  border-radius: 4px;
  color: #FFFFFF;
}
.content {
  background-color: #FFFFFF;
}

.navbar {
  background-color: #363636;
  color: #FFFFFF;
}
.navbar-item .icon {
  color: #FFFFFF;
  margin-right: 15px;
  margin-bottom: 3px;
}
.navbar-item .title {
  color: #FFFFFF;
  margin-bottom: 0;
}
.navbar-item .subtitle {
  color: #FFFFFF;
  font-weight: bold;
}

.home-wrapper {
}
.title.is-fullheight {
  height: 200px;
  line-height: 200px;
}

.customer-wrapper {
  margin: 20px;
  margin-left: 10px;
}
.customer-wrapper .control .input {
  width: 300px;
}
.customer-wrapper .control .button {
  background-color: #00D1B3;
  border-color: #00D1B3;
  color: #FFFFFF;
  font-weight: 600;
}
.customer-wrapper .control .button:hover {
  border-color: #00D1B3;
  color: lightgrey;
}
.customer-wrapper .empty {
  font-weight: bold;
  color: grey;
}

.customer-detail-wrapper {
  margin: 20px;
  margin-left: 10px;
}
.customer-detail-wrapper .title {
  font-size: 30px;
  font-weight: bold;
}
.single {
  margin: 20px;
  margin-left: 10px;
}
.single .title{
  font-size: 20px;
  font-weight: 500;
}

.order-wrapper {
  text-align: center;
}
.title.is-fullheight {
  height: 200px;
  line-height: 200px;
}

.cost-wrapper {
  text-align: center;
}
.title.is-fullheight {
  height: 200px;
  line-height: 200px;
}

.notification-wrapper {
  text-align: center;
}
.title.is-fullheight {
  height: 200px;
  line-height: 200px;
}

.title.is-fullheight {
